<template>
    <section class="guidance-wrap">
        <c-title :title="title" :link="'www.baidu.com'"></c-title>
        <div class="guidance">
            <div class="nav">
                <div class="c-row">
                    <div class="c-span8 item"  @click="changeTap(0)" :class="{active:0==ins}">治安
    
                    </div>
                    <div class="c-span8 item" @click="changeTap(1)" :class="{active:1==ins}">公交</div>
                    <div class="c-span8 item" @click="changeTap(2)" :class="{active:2==ins}">内保</div>
                    <div class="c-span8 item" @click="changeTap(3)" :class="{active:3==ins}">交通</div>
                    <div class="c-span8 item" @click="changeTap(4)" :class="{active:4==ins}">网安</div>
                    <div class="c-span8 item" @click="changeTap(5)" :class="{active:5==ins}">出入境</div>
                    <div class="c-span8 item" @click="changeTap(6)" :class="{active:6==ins}">人口</div>
                    <div class="c-span8 item" @click="changeTap(7)" :class="{active:7==ins}">禁毒</div>
    
                </div>
    
            </div>
            <div class="list">
                <ul>
                    <li class='c-line-clamp1 items' v-for="(item, index) in items" :key="index">
                        <a :href="item.href"><span>•</span>{{item.name}}</a>
                    </li>
                </ul>
    
            </div>
        </div>
    </section>
</template>

<script>
    import cTitle from '@/components/title';
    export default {
    
        components: {
            cTitle
        },
        data() {
            return {
                title: '办事指南',
                ins: 0,
                items: [],
                datas: [{
                        href: 'www.baidu.com',
                        name: '刑事技术现成勘察1'
                    },
                    {
                        href: 'www.baidu.com',
                        name: '刑事技术现成勘察2'
                    },
                    {
                        href: 'www.baidu.com',
                        name: '刑事技术现成勘察2'
                    },
                    {
                        href: 'www.baidu.com',
                        name: '刑事技术现成勘察3'
                    },
                    {
                        href: 'www.baidu.com',
                        name: '刑事技术现成勘察5'
                    }
                ]
            };
        },
        created() {
            this.changeTap(0)
        },
        methods: {
            loadMore(ins) {
                this.items =[];
                this.datas.forEach((item, index) => {
                    this.items.push({
    
                        href: 'www.baidu.com',
                        name: '刑事技术现成勘察' + ins
    
                    });
                });
            },
            changeTap(ins) {
                this.ins = ins;
                this.loadMore(ins)
            }
    
        }
    };
</script>

<style scoped lang='less'>
    .guidance {
        padding: 0px 13px 7.7px 13px;
    }
    
    
    a {
        text-decoration: none;
    }
    
    .nav {
        position: relative;
        .item {
            text-align: center;
            line-height: 40px;
            position: relative;
            font-size: 12px;
        }
        .active {
            color: #1d8fe1;
            border-bottom: 1px solid #55b7f4;
        }
    }
    
    .nav:after {
        content: "  ";
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 1px;
        background-color: #ebeef0;
        -webkit-transform: scaleY(.5);
        transform: scaleY(.5);
    }
    
    .list {
        .items {
            a {
                line-height: 27px;
                font-size: 12px;
                color: #000;
            }
        }
    }
</style>



