<template>
    <section class="index-page">
        <c-top></c-top>
        <c-message></c-message>
        <Cbusiness></Cbusiness>
        <Cguidance></Cguidance>
        <Cservice></Cservice>
        <Cnews></Cnews>
        <Cservicebox></Cservicebox>
        <Cnav :ins="0"></Cnav>
        <Cmask :value="masklock" @visible-change="showMask" ></Cmask>
    </section>
</template>

<script>
    import cTop from './comps/top.vue';
    import cMessage from './comps/message_2.vue';
    import Cbusiness from './comps/business_handling_3.vue';
    import Cguidance from './comps/guidance_4.vue';
    import Cservice from './comps/service-seeking_5.vue';
    import Cnews from './comps/new_6.vue';
    import Cservicebox from './comps/service-seeking_7.vue';
    import Cnav from '@/components/nav.vue';
    import Cmask from './comps/mask';
    import hub from '@/common/hub';
    export default {
        components: {
            cTop,
            cMessage,
            Cbusiness,
            Cguidance,
            Cservice,
            Cnews,
            Cservicebox,
            Cnav,
            Cmask
        },
        data() {
            return {
                masklock: false
            };
        },
        beforeDestroy() {
            this.removeEvent();
        },
        created() {
            this.init();
        },
        mounted() {
    
        },
        methods: {
            loadMore() {
    
            },
            init() {
                this.initEvent();
            },
            initEvent() {
                hub.$on('showMask', this.showMask);
            },
            removeEvent() {
                hub.$off('showMask', this.showMask);
    
            },
            showMask(st){
                this.masklock = st;
            },
            send() {
    
                this.$http
                    .post('/hackathon/infer', {
                        id: 111,
                        is_default: 111,
                        tg_default: 1
                    })
                    .then(err => {
                        console.log(err);
                    });
            },
            getArticleList() {
                let url = '/youyi/common/getArticle';
                let params = this.search;
                this.$http({
                    method: 'get',
                    url,
                    params
                }).then(res => {
                    let list = res.data.articleInfo || [];
                    if (list.length) {
                        list.forEach(item => {
                            this.list.push(item);
                        });
                        this.busy = false;
                        this.search.page++;
                    } else {
                        this.isNoMoreData = true;
                    }
                });
            }
        }
    };
</script>

<style>
    body {
        
    }
    
    .index-page {
        position: relative;
    }
</style>



